<script>
export default {
  data() {
    return {
      Nav: []
    }
  },
  methods: {
    async getNav() {
      let Resources = await this.$api.getNav();
      this.Nav = Resources.data.nav
    }
  },
  mounted() {
    this.getNav()
  }
}
</script>

<template>
  <div class="nav">
    <div class="content">
      <ul>
        <li>
          <a href="/#/zh-cn">
            <img src="@/assets/images/Nav/icon/home.png" alt="">
          </a>
        </li>
        <li v-for="value in Nav" :key="value.uid">
          <a :href="value.links">{{ value.name }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<style scoped lang="stylus">
.nav
  width 100%
  height 45px
  background #725BA1

  .content
    margin 0 auto
    width 1200px
    height 100%

    > ul
      display flex
      width 100%
      height 100%

      > li
        display flex
        align-items center
        padding 0 25px

        > a
          color #ffffff

      > li:first-of-type
        > a
          > img
            width 20px

      > li:hover
        background #361e69
</style>